.login-background {
    background: url("../../assets/images/background-images/background3.jpg") no-repeat fixed right;
    background-size: auto 100%;
}

.login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 290px;
    height: 320px;
    border-radius: 15px;
    background-color: #efefef7a;

    .login-logo {
        width: 40%;
        height: auto;
        margin-top: 70px;
        margin-bottom: 20px;
    }

   
    .login-input-box {
        display: flex;
        justify-content: center;

        position: relative;

        label {
            position: absolute;
            font-size: 14px;
            top: 18px;
            right: 5px;
            transition: all 0.3s ease;
            color: #fdfdfd;
        }

        input {
            margin-top: 10px;
            width: 240px;
            height: 40px;
            border: 0;
            border-radius: 5px;
            text-align: center;
            color: #fdfdfd;
            background-color: #383838;

            &::placeholder {
                opacity: 0;
            }

            &:focus {
                &::placeholder {
                    opacity: 1;
                }

                border: 1.5px solid var(--primary-blue);
                outline: 0;
                color: #fdfdfd;
            }

            &:focus,
            &:not(:placeholder-shown),
            &:-webkit-autofill {
                &+label {
                    top: 10px;
                    right: -2px;
                    padding: 0 3px 0 5px;
                    font-size: 12px;
                    transform: scale(0.75)
                }
            }
        }

        .show-hide-password-icon {
            position: absolute;
            top: 19px;
            left: 5px;
            width: 20px;
            cursor: pointer;
        }
    }

    .login-input-box:nth-child(1) {
        margin-top: 35px;
    }

    .login-button {
        margin-top: 35px;
        height: 35px;
        width: 240px;
        border: none;
        border-radius: 7px;
        background-color: var(--primary-black);
        color: #f1f1f1;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
            cursor: pointer;
        }
    }

    .signup {
        color: #0a0a0a;
        margin-top: 10px;
        margin-bottom: 0;
        font-size: 14px;
        cursor: pointer;
    }
}

.wrong-login-message {
    color: #ededed;
    background-color: #ff000091;
    border-radius: 10px;
    padding: 2px 3px;
    margin-top: 10px;
    opacity: 0;
    font-size: 14px;
}

.wrong-login-message-show {
    opacity: 1;
}

@media (min-width: 600px) {
    .login-container {
        width: 420px;
        height: 320px;

        .login-button,
        .login-input-box input {
            width: 310px;
        }
    }

    .login-background {
        background-size: 100% 100%;
    }
}

