.services-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 12px;

    a {
        text-decoration: none;
    }
}

.service-container {
    width: 180px;
    height: 140px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    transition: all 0.3s ease;
    // background-color: var(--deep-cove);
    background-color: var(--primary-blue);
    // background-color: var(--m-1);
    // color: var(--orange);
    color: #efefef;

    p {
        display: flex;
        opacity: 0;
    }

    img {
        height: 70px;
        width: auto;
    }

    &:hover {
        transform: scale(1.04);
        p {
            opacity: 1;
        }
        // img {
        //     display: none;
        // }
    }
}

@media (min-width: 768px) {

    .service-container {
        width: 300px;
        height: 200px;

        img {
            height: 100px;
        }

    }



}