.modal-container {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3a3a3a7d;
    z-index: 5;

    .modal-wrapper {
        background-color: aliceblue;
        border: 1px solid var(--purple);
        padding: 12px;
        display: flex;
        flex-direction: column;
        border-radius: 15px;
        margin: 20px;
    }
}