.copy-right-message-container {
    position: fixed;
    bottom: 3px;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
    height: 20px;

    .copy-right-message-wrapper {
        width: max-content;
        background-color: var(--background-color-for-container);
        border-radius: 10px;
        padding: 0 1px;

        a {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            color: #f0f8ff;
        }

        p {
            margin: 0 5px 0 0;
            padding: 0;
        }

        .copy-right-message-logo {
            width: 20px;
            height: auto;
            margin: auto;
        }

    }
}