/// *** start hamburger menu ***///

@media (max-width: 767px) {
    .show-sidebar {
        right: 0 !important;
    }

    .sidebar {
        display: flex;
        flex-direction: column;
        background-color: #ffffffc0;
        width: 100vw;
        height: 100vh;
        z-index: 5;
        position: absolute;
        top: 0;
        right: -100%;
        transition: all 0.3s;


        .list-items-container {
            margin-top: calc(var(--nabvar-height) + 4%);

            .list-items {
                background-color: #ffffff69;

                .list-item {
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    // margin: 7px 25px 0;
                    margin: 0 25px;
                    padding: 4px 0;
                    cursor: pointer;
                    width: max-content;
                    transition: all ease 0.3s;
                    white-space: nowrap;
                    width: calc(100% - 25px);
                    overflow: hidden;

                    .list-item-icon {
                        height: 25px;
                        width: auto;
                        margin-left: 6px;
                        transition: all 0.3s ease;
                    }


                    .link {
                        color: inherit;
                        text-decoration: none;
                        transition: all 0.3s;
                        width: 100%;
                        text-align: center;
                    }




                    &:first-child {
                        // background-color: #1a2753b3;
                        // background-color: var(--deep-cove);
                        background-color: var(--purple);
                        color: #f1f1f1;
                        // background-color: var(--blue-green);
                        align-items: center;
                        margin: 0;
                        padding-right: 19px;
                        width: 100%;
                        cursor: default;
                        img {
                            height: 30px;
                            width: 35px;
                        }
                    }

                }
            }
        }
    }

    // start hamburger menu style
    .hamburger-menu-container {
        z-index: 9;

        .hamburger-lines {
            display: block;
            height: 24px;
            width: 30px;
            position: absolute;
            top: 21px;
            left: 20px;
            z-index: 2;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }

        .checkbox {
            position: absolute;
            display: block;
            height: 32px;
            width: 32px;
            top: 20px;
            left: 20px;
            z-index: 5;
            opacity: 0;
            cursor: pointer;
        }

        .hamburger-lines .line {
            display: block;
            height: 4px;
            width: 100%;
            border-radius: 10px;
            background: #0e2431;
        }

        .hamburger-lines .line1 {
            transform-origin: 0% 0%;
            transition: transform 0.4s ease-in-out;
        }

        .hamburger-lines .line2 {
            transition: transform 0.2s ease-in-out;
        }

        .hamburger-lines .line3 {
            transform-origin: 0% 100%;
            transition: transform 0.4s ease-in-out;
        }

        input[type="checkbox"]:checked~.menu-items {
            transform: translateX(0);
        }

        input[type="checkbox"]:checked~.hamburger-lines .line1 {
            transform: rotate(45deg);
        }

        input[type="checkbox"]:checked~.hamburger-lines .line2 {
            transform: scaleY(0);
        }

        input[type="checkbox"]:checked~.hamburger-lines .line3 {
            transform: rotate(-45deg);
        }

    }

    // end hamburger menu style
}

/// *** end hamburger menu ***///



/// *** desktop sidebar ***///
@media (min-width: 768px) {
    .sidebar {
        display: flex;
        flex-direction: column;
        height: calc(100vh - var(--nabvar-height) - 12%);
        width: var(--sidebar-width-in-collapse-mode);
        // border-radius: 15px 0 0 15px;
        border-radius: 15px;
        border: 2px solid var(--blue-green);
        transition: all ease 0.3s;
        position: absolute;
        top: var(--nabvar-height);
        z-index: 1;
        background-color: var(--background-color-for-container);
        margin: 4% 2% 2% 1%;
        overflow: scroll;

        .pin-container {
            opacity: 0;
            transition: all 0.3s;
            display: flex !important;
            position: relative;
            top: 10px;
            left: 10px;

            img {
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0px;
                cursor: pointer;
                display: none;
            }
        }


        .list-items-container {
            margin-top: 30px;

            .list-items {
                background-color: #ffffff69;

                .list-item {
                    display: flex;
                    flex-direction: row;
                    margin: 4px 0;
                    cursor: pointer;
                    width: max-content;
                    transition: all ease 0.3s;
                    white-space: nowrap;
                    width: 100%;
                    overflow: hidden;

                    .list-item-icon {
                        height: 25px;
                        width: 25px;
                        margin-left: 6px;
                        transition: all 0.3s ease;
                    }

                    .list-item-text {
                        opacity: 0;
                        transition: all 0.3s ease;
                    }

                    .link{
                        color: inherit;
                        text-decoration: none;
                        transition: all 0.3s;
                        padding: 4px 20px
                    }

                    &:hover {
                        padding-right: 7px;
                    }

                    &:first-child {
                        background-color: var(--purple);
                        color: #f1f1f1;
                        align-items: center;
                        margin: 0;
                        padding-right: 19px;
                        width: 100%;
                        cursor: default;

                        img {
                            height: 30px;
                            width: 35px;
                        }
                    }

                    .active-link {
                        padding: 4px 16px;

                        .list-item-icon {
                            border-radius: 7px;
                            border: 2px solid var(--purple);
                            color: #f1f1f1;
                            padding: 2px;
                            height: 35px;
                            width: 35px;
                            transition: all 0s;
                        }
                    }
                }
            }
        }

        &:hover {
            width: var(--sidebar-width-in-expand-mode) !important;
            position: absolute;

            .pin-container {
                opacity: 1;

                img {
                    display: flex;
                }
            }

            .list-item {
                .list-item-text {
                    opacity: 1 !important;
                }
            }

            .active-link {
                border-radius: 15px;
                background-color: var(--purple);
                color: #f1f1f1;
                padding: 1px 5px;
                .list-item-icon {
                    background-color: #ffffff00 !important;
                    border: none !important;
                }
            }
        }

    }

    .hamburger-menu-container {
        display: none;
    }



}