.custom-navbar {
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    height: var(--nabvar-height);
    width: 96%;
    margin: 2%;
    // z-index: 5;
    border-color: var(--blue-green);
    // background-color: var(--blue-green);
    background-color: var(--background-color-for-container);
    position: fixed;
    border-radius: 15px;

    .logo-container {
        display: flex;
        justify-content: center;
        align-items: center;

        .logo-image {
            background-color: var(--purple);
            height:calc(var(--nabvar-height) - 8px);
            width:  auto;
            margin: 0 10px;
            border-radius: 5px;

        }
    }



    .user-profile-container {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;


        margin: auto;

        .user-profile {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            height: 100%;
            margin: 0 15px;
            font-size: 14px;
        }

        .user-profile-image {
            width: 35px;
            height: 35px;
            margin-left: 5px;
            cursor: pointer;
        }

        .user-profile-popup {
            position: absolute;
            top: var(--nabvar-height);
            left: 0;
            width: 180px;
            height: 80px;
            border-radius: 7px;
            background-color: red;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }

    .hamburger-menu-placeholder {
        display: flex;
        width: 50px;

    }

}


@media (min-width: 768px) {
    .custom-navbar {

        .hamburger-menu-placeholder {
            display: none;
        }

        .user-profile-container {
            margin: 0 !important;
            padding: 1px;

        }
    }

}