* {
    font-family: "iranyekanweb_regular" !important;
}

label {
    cursor: pointer;
}

ul {
    padding: 0;
    margin: 0;

    li {
        list-style: none;
        padding: 0;
        margin: 0;
    }
}

option,
select {

    height: 40px;
    border: 1px solid var(--primary-blue);
    border-radius: 5px;
    padding-right: 5px;
    text-align: right;
    background: inherit;
    background-color: #ffffffcc;

    &:focus {
        outline: 1px solid #ffffff;
        border: 2px solid var(--primary-blue);
    }
}




.h-100vh {
    height: 100vh;
}

.w-100vh {
    width: 100vw;
}


.direction-ltr {
    direction: ltr;
}

.bi-custom-button {
    height: 38px;
    padding: 0 15px;
    margin: 5px;
    border: none;
    border-radius: 7px;
    background-color: var(--wood-light);
    color: #f1f1f1;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        cursor: pointer;
    }
}



/* ***** start hide up/down arrows input *****/
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

/***** end hide up/down arrows input *****/


/***** start  Hide scrollbar *****/
/* Hide scrollbar for Chrome, Safari and Opera */
*::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
* {
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}

/***** end  Hide scrollbar *****/


/* ***** start checkBox input animation *****/
// round slider
.switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 18px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 19px;
    width: 19px;
    left: 0px;
    bottom: 0px;
    background-color: #2196F3;
    transition: .4s;
}

input:checked+.slider {
    background-color: #2196F3;
}

input:checked+.slider::before {
    background-color: #ccc;
}

input:focus+.slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
    transform: translateX(18px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

/* ***** end checkBox input animation *****/
