.customized-container {
    // height: calc(100vh - var(--nabvar-height));
    height: calc(100vh - var(--nabvar-height) - 12%);
    // width: calc(100vw - var(--sidebar-width-in-collapse-mode));
    width: 100vw;
    height: calc(100vh - var(--nabvar-height) - 12%);
    width: 100vw;
    position: absolute;
    top: var(--nabvar-height);
    margin-top: 4%;
}

.background {
    background: url("../images/background-images/background1.jpg") no-repeat fixed left;
    // background: url("../images/hotel-background.jpg") no-repeat fixed left;
    background-size: 100% 100%;
}

.customized-wrapper {
    position: relative;
    width: 96%;
    margin-right: 2%;
    height: 100%;
    // max-height: 98%;
    padding: 15px;
    // display: flex;
    display: block;
    flex-direction: column;
    // align-items: center;
    // justify-content: center;
    background-color: var(--background-color-for-container);
    border-radius: 15px;
    overflow: scroll;

    & {
        -ms-overflow-style: none;
        /* Internet Explorer 10+ */
        scrollbar-width: none;
        /* Firefox */
    }

    &::-webkit-scrollbar {
        display: none;
        /* Safari and Chrome */
    }
}

.title-session {
    border-radius: 13px;
    text-align: center;
    padding: 10px 0;
    background-color: aliceblue;
    width: 100%;
    color: var(--blue-green);
}



.input-box {
    display: flex;
    justify-content: center;
    position: relative;

    label {
        position: absolute;
        // background-color: #ffffff;
        top: 17px;
        right: 25px;
        transition: all 0.3s ease;
    }

    input {
        height: 40px;
        border: 1px solid var(--primary-blue);
        border-radius: 5px;
        padding-right: 5px;
        text-align: center;
        background: inherit;
        background-color: #ffffffcc;

        margin-top: 10px;
        width: 90%;

        &::placeholder {
            opacity: 0;
        }

        &:focus {
            &::placeholder {
                opacity: 1;
            }
        }

        &:focus,
        &:not(:placeholder-shown),
        &:-webkit-autofill {
            outline-color: var(--primary-blue);

            &+label {
                top: 9px;
                right: 22px;
                padding: 0 3px 0 5px;
                font-size: 12px;
                transform: scale(0.8)
            }
        }
    }

    select {
        height: 40px;
        margin-top: 10px;
        width: 90%;
    }

    input,
    select,
    option {
        cursor: pointer;
    }
}

.input-for-editbox-in-table {
    height: 32px;
    border: 1px solid var(--primary-blue);
    border-radius: 5px;
    padding-right: 5px;
    text-align: center;
    background: inherit;
    background-color: #ffffffcc;
    width: 150px;
}

.option-for-editbox-in-table {
    width: 150px;
    height: 32px;
}



.table-container-scroll {
    overflow: hidden;
    overflow-x: scroll;
}

.bi-table-style {

    background-color: var(--table-background);
    // width: fit-content;
    color: #212529;
    vertical-align: top;
    border-color: #dee2e6;
    margin-top: 1vh;

    // *** start table radius for table ***
    border-collapse: collapse;
    border-radius: 7px;
    /* hide standard table (collapsed) border */
    border-style: hidden;
    /* this draws the table border  */
    box-shadow: 1px 1px 1px #666;
    // *** end table radius for table ***


    thead {
        border-bottom: 1px solid #000000;

        tr {
            border-bottom: 1px solid #a0a0a0;
            height: 55px;
            font-size: 14px;
        }
    }

    tbody {
        tr {
            background-color: #c2c03833;

            &:nth-child(odd) {
                background-color: #a489722c;
            }
        }
    }

    td,
    th {
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        padding: 0.2rem 0.5rem;
        border: 1px solid rgb(138, 138, 138);
        outline: none;
    }

    td {
        height: 40px;
    }

}

.bi-row-select-enable {
    tbody {
        tr {
            &:hover {
                cursor: pointer;
                background-color: #faa536 !important;

                td {
                    border: 1px solid;
                }
            }
        }

        .item-slected {
            background-color: #faa536 !important;
        }
    }
}








@media (min-width: 768px) {
    .customized-wrapper {
        width: calc(94% - var(--sidebar-width-in-collapse-mode));
        margin-right: calc(var(--sidebar-width-in-collapse-mode) + 4%);
    }



}